nav {
    background: rgba(0, 0, 0, 0.404);
    width: max-content;
    display: block;
    padding: 0.7rem 1.5rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    gap: 0.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
}

nav a {
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var(--colorLight);
    font-size: 1.1rem;
}

nav a:hover {
    background: rgba(0, 0, 0, 0.404)
}

nav a.currentNav {
    background: var(--colorBg);
    color: var(--colorWhite);
}